<template>
	<div>
		<el-dialog class="edit_form" v-model="formVisible" :title="formTitle" width="70%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row>
					<el-col :span="12">
						<el-form-item label="" prop="questionname">
							<el-input :readonly="!isAdd" v-model="form.questionname" placeholder="" type="textarea"></el-input>
						</el-form-item>
					</el-col>
					<!--  -->
					<el-col :span="24" v-if="form.type==0">
						<el-form-item label="" prop="optionList">
							<el-input class='list_inpGroup' :readonly="!isAdd" v-for="(item,index) in form.optionList" :key="index" v-model="item.text"
								style="margin-bottom: 6px;" :placeholder="`${codeChange(index)}`">
								<template #prepend>{{codeChange(index)}}</template>
								<template #append>
									<el-input :readonly="!isAdd" v-model="item.score" placeholder="" size="small" style="width:100px;" type="number"></el-input>
									<el-button v-if="isAdd" style="margin-left: 10px;" :icon="Delete" @click.native="delOption(index)" />
								</template>
							</el-input>
							<div style="width: 100%;text-align: center;padding-top: 10px;"
								v-if="form.optionList.length<4&&isAdd"><el-button circle :icon="Plus" type="success"
									@click.native="addOption"></el-button></div>
						</el-form-item>
					</el-col>
					<!--  -->
					<el-col :span="24">
						<el-form-item label="" prop="analysis">
							<el-input :readonly="!isAdd" v-model="form.analysis" placeholder="" type="textarea"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="sequence">
							<el-input class="list_inp" :readonly="!isAdd" v-model="form.sequence" placeholder="" type="number" :min="1"
								:max="20"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick"></el-button>
					<el-button class="confirm_btn" type="primary" @click="save">
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	import {
		Delete,
		Plus
	} from '@element-plus/icons-vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'examquestionbank'
	const formName = ''
	//
	//form
	const form = ref({
		papername: '',
		paperid: '',
		questionname: '',
		answer: '',
		analysis: '',
		score: '',
		sequence: '',
		type: 0,
		optionList: [{
			text: '',
			score: 1
		},{
			text: '',
			score: 1,
		}],
	})
	const disabledForm = ref({
		name: false,
		time: false,
		status: false,
	})
	const formVisible = ref(false)
	const formTitle = ref('')
	//
	//
	const validateNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	const rules = ref({
		questionname: [{
			required: true,
			message: '',
			trigger: 'blur'
		}, ],
		analysis: [{
			required: true,
			message: '',
			trigger: 'blur'
		}, ],
		sequence: [{
			required: true,
			message: '',
			trigger: 'blur'
		}, ],
	})
	//
	const formRef = ref(null)
	const id = ref(0)
	//
	const getUUID = () => {
		return new Date().getTime();
	}
	//
	const resetForm = () => {
		form.value = {
			papername: '',
			paperid: '',
			questionname: '',
			answer: '',
			analysis: '',
			score: '',
			sequence: '',
			type: 0,
			optionList: [{
				text: '',
				score: 1
			},{
				text: '',
				score: 1,
			}],
		}
	}
	//info
	const getInfo = () => {
		context?.$http({
			url: `examquestionbank/info/${id.value}`,
			method: 'get'
		}).then(res => {
			if (res.data.data.options) {
				res.data.data.optionList = JSON.parse(res.data.data.options)
			} else {
				res.data.data.optionList = []
			}
			if (res.data.data.type == 1) {
				res.data.data.answers = res.data.data.answer.split(',')
			}
			form.value = res.data.data
			formVisible.value = true
		})
	}
	//
	const isAdd = ref(false)
	const init = (refid = null, type = null) => {
        if(!refid){
          formTitle.value = ''
          isAdd.value = true
        }else if(type=='edit'){
          formTitle.value = ''
          isAdd.value = true
        }else{
          formTitle.value = ''
          isAdd.value = false
        }
		resetForm()
		if (refid) {
			id.value = refid
			getInfo()
		}
		formVisible.value = true
		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
		})
	}
	//
	const codeChange = (type) => {
		let arr = ['A', 'B', 'C', 'D']
		return arr[type]
	}
	//
	const delOption = (index) => {
		form.value.optionList.splice(index, 1)
	}
	//
	const addOption = () => {
		form.value.optionList.push({
			text: '',
			score: 1
		})
	}
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const save = () => {
		formRef.value.validate((valid) => {
			if (valid) {
				if (form.value.optionList.length > 1) {
					for(let x in form.value.optionList){
						form.value.optionList[x].code = codeChange(x)
					}
					form.value.options = JSON.stringify(form.value.optionList)
				} else {
					context?.$toolUtil.message('', 'error')
					return false
				}
				context?.$http({
					url: `examquestionbank/${!form.value.id ? "save" : "update"}`,
					method: 'post',
					data: form.value
				}).then(res => {
					context.$message.success(``)
                    formVisible.value = false
                    emit('formModelChange')
				})
			}
		})
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			// 
			.el-form-item__content{

				// 
				.list_inp {
				}
				// 
				.list_sel {
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
				// 
				.el-textarea__inner {
				}
				// 
				.list_inpGroup {

					.el-input-group__prepend {
					}

					.el-input__wrapper {
					}

					.el-input-group__append {

						.el-input {
						}

						.el-button {
						}
					}
				}
			}
		}
	}

</style>