<template>
	<div>
		<div class="center_view">
			<div class="list_search_view">
				<el-form :model="searchQuery" class="search_form" >
					<div class="search_view">
						<div class="search_label">
							
						</div>
						<div class="search_box">
							<el-select
								class="search_sel"
								clearable
								v-model="searchQuery.zhanghao"
								placeholder=""
								>
								<el-option v-for="item in zhanghaoLists" :label="item" :value="item"></el-option>
							</el-select>
						</div>
					</div>
					<div class="search_btn_view">
						<el-button class="search_btn" type="primary" @click="searchClick()" size="small"></el-button>
					</div>
				</el-form>
				<div class="btn_view">
					<el-button class="add_btn" type="success" @click="addClick" v-if="btnAuth('ceshipingfen','')">
						
					</el-button>
					<el-button class="del_btn" type="danger" :disabled="selRows.length?false:true" @click="delClick(null)"  v-if="btnAuth('ceshipingfen','')">
						
					</el-button>
                    <el-button class="statis_btn" type="warning" @click="echartClick1" v-if="btnAuth('ceshipingfen','')">
                        
                    </el-button>
                    <el-button class="statis_btn" type="warning" @click="echartClick2" v-if="btnAuth('ceshipingfen','')">
                        
                    </el-button>
				</div>
			</div>
			<el-table
				v-loading="listLoading"
				border
				:stripe='false'
				@selection-change="handleSelectionChange"
				ref="table"
				v-if="btnAuth('ceshipingfen','')"
				:data="list"
				@row-click="listChange">
				<el-table-column :resizable='true' align="left" header-align="left" type="selection" width="55" />
				<el-table-column label="" width="70" :resizable='true' align="left" header-align="left">
					<template #default="scope">{{ (listQuery.page-1)*listQuery.limit+scope.$index + 1}}</template>
				</el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="zixunshizhanghao"
					label="">
					<template #default="scope">
						{{scope.row.zixunshizhanghao}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="lianxifangshi"
					label="">
					<template #default="scope">
						{{scope.row.lianxifangshi}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="pingfenshijian"
					label="">
					<template #default="scope">
						{{scope.row.pingfenshijian}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="zhanghao"
					label="">
					<template #default="scope">
						{{scope.row.zhanghao}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="shouji"
					label="">
					<template #default="scope">
						{{scope.row.shouji}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="pingfen"
					label="">
					<template #default="scope">
						{{scope.row.pingfen}}
					</template>
				</el-table-column>
				<el-table-column label="" min-width="140" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						<el-button v-if="scope.row.ceshibaogao" size="small" @click="download(scope.row.ceshibaogao)"></el-button>
						<el-button v-else disabled link size="small"></el-button>
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="pingfenbeizhu"
					label="">
					<template #default="scope">
						{{scope.row.pingfenbeizhu}}
					</template>
				</el-table-column>
				<el-table-column label="" width="300" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						<el-button class="view_btn" type="info" v-if=" btnAuth('ceshipingfen','')" @click="infoClick(scope.row.id)">
							
						</el-button>
						<el-button class="edit_btn" type="primary" @click="editClick(scope.row.id)" v-if=" btnAuth('ceshipingfen','')">
													</el-button>
						<el-button class="del_btn" type="danger" @click="delClick(scope.row.id)"  v-if="btnAuth('ceshipingfen','')">
													</el-button>
						<el-button class="cross_btn" v-if="btnAuth('ceshipingfen','')" type="success" @click="jiankangjianyiCrossAddOrUpdateHandler(scope.row,'cross','','','','')">
							
						</el-button>
					</template>
				</el-table-column>
			</el-table>
			<el-pagination
				background
				:layout="layouts.join(',')"
				:total="total"
				:page-size="listQuery.limit"
                v-model:current-page="listQuery.page"
				prev-text="<"
				next-text=">"
				:hide-on-single-page="false"
				:style='{}'
				:page-sizes="[10, 20, 30, 40, 50, 100]"
				@size-change="sizeChange"
				@current-change="currentChange"  />
		</div>
		<formModel ref="formRef" @formModelChange="formModelChange"></formModel>
		<!--  -->
		<el-dialog v-model="echartVisible" title="" width="70%">
			<el-tabs v-model="echartActive" class="demo-tabs" @tab-change="echartTabClick" type="card">
                <el-tab-pane label="" name="1" v-if="btnAuth('ceshipingfen','')"></el-tab-pane>
                <el-tab-pane label="" name="2" v-if="btnAuth('ceshipingfen','')"></el-tab-pane>
			</el-tabs>
            <div v-if="echartActive==1" id="pingfenEchart1" style="width:100%;height:600px;"></div>
            <div v-if="echartActive==2" id="zhanghaoEchart2" style="width:100%;height:600px;"></div>
			<template #footer>
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="echartVisible=false"></el-button>
				</span>
			</template>
		</el-dialog>
		<jiankangjianyiFormModel ref="jiankangjianyiFormModelRef" @formModelChange="formModelChange"></jiankangjianyiFormModel>
	</div>
</template>
<script setup>
	import axios from 'axios'
    import moment from "moment"
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		onMounted,
		watch,
		computed,
		inject
	} from 'vue'
	import {
		useRoute,
		useRouter
	} from 'vue-router'
	import {
		ElMessageBox
	} from 'element-plus'
	import {
		useStore
	} from 'vuex';
	const store = useStore()
	const user = computed(()=>store.getters['user/session'])
	const avatar = ref(store.state.user.avatar)
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	import formModel from './formModel.vue'
	//

	const tableName = 'ceshipingfen'
	const formName = ''
	const route = useRoute()
	//
	onMounted(()=>{
	})
	//
	const list = ref(null)
	const table = ref(null)
	const listQuery = ref({
		page: 1,
		limit: 20,
		sort: 'id',
		order: 'desc'
	})
	const searchQuery = ref({})
	const selRows = ref([])
	const listLoading = ref(false)
	const listChange = (row) =>{
		nextTick(()=>{
			//table.value.clearSelection()
			table.value.toggleRowSelection(row)
		})
	}
	//
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		params['sort'] = 'id'
		params['order'] = 'desc'
		if(searchQuery.value.zhanghao&&searchQuery.value.zhanghao!=''){
			params['zhanghao'] = searchQuery.value.zhanghao
		}
		context.$http({
			url: `${tableName}/page`,
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
		})
	}
	//
	const delClick = (id) => {
		let ids = ref([])
		if (id) {
			ids.value = [id]
		} else {
			if (selRows.value.length) {
				for (let x in selRows.value) {
					ids.value.push(selRows.value[x].id)
				}
			} else {
				return false
			}
		}
		ElMessageBox.confirm(`${formName}`, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			context.$http({
				url: `${tableName}/delete`,
				method: 'post',
				data: ids.value
			}).then(res => {
				context?.$toolUtil.message('', 'success',()=>{
					getList()
				})
			})
		}).catch(_ => {})
	}
	//
	const handleSelectionChange = (e) => {
		selRows.value = e
	}
	//
	//
	const total = ref(0)
	const layouts = ref(["total","prev","pager","next","sizes","jumper"])
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	//
	//
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	//
	const zhanghaoLists = ref([])
	const getzhanghaoLists = () => {
		context.$http({
			url: 'option/yonghu/zhanghao',
			method:'get'
		}).then(res=>{
			zhanghaoLists.value = res.data.data
		})
	}
	getzhanghaoLists()
	const searchClick = () => {
		listQuery.value.page = 1
		getList()
	}
	//
	const formRef = ref(null)
	const formModelChange=()=>{
		searchClick()
	}
	const addClick = ()=>{
		formRef.value.init()
	}
	const editClick = (id=null)=>{
		if(id){
			formRef.value.init(id,'edit')
			return
		}
		if(selRows.value.length){
			formRef.value.init(selRows.value[0].id,'edit')
		}
	}

	const infoClick = (id=null)=>{
		if(id){
			formRef.value.init(id,'info')
		}
		else if(selRows.value.length){
			formRef.value.init(selRows.value[0].id,'info')
		}
	}
	// 
	// 
	const preClick = (file) =>{
		if(!file){
			context?.$toolUtil.message('','error')
		}
		window.open(context?.$config.url + file)
	}
	// 
	const download = (file) => {
		if(!file){
			context?.$toolUtil.message('','error')
		}
		const a = document.createElement('a');
		a.style.display = 'none';
		a.setAttribute('target', '_blank');
		file && a.setAttribute('download', file);
		a.href = context.$config.url + file;
		document.body.appendChild(a);
		a.click();
		document.body.removeChild(a);
	}
    import '@/assets/js/echarts-theme'
	// 1
	const echarts = inject("echarts")
	const echartVisible = ref(false)
	const echartClick1 = ()=>{
		echartActive.value = '1'
		echartVisible.value = true
		nextTick(()=>{
			var pingfenEchart1 = echarts.init(document.getElementById("pingfenEchart1"),'theme');
			context.$http({
				url: `${tableName}/sectionStat/pingfen`,
				method: 'get'
			}).then(res=>{
				let obj = res.data.data
				let xAxis = [];
				let yAxis = [];
				let dataList = []
				for(let i=0;i<obj.length;i++){
				    xAxis.push(obj[i].pingfen);
				    yAxis.push(parseFloat((obj[i].total)));
                    dataList.push({
				        value: parseFloat((obj[i].total)),
				        name: obj[i].pingfen
				    })
				}
				var option = {};
				option = {
    title: {
        text: '',
        left: 'center'
    },
    tooltip: {
        trigger: 'item',
        formatter: '{b} : {c}'
    },
    xAxis: {
        data: xAxis,
        type: 'category',
        axisLabel: {
        interval: 0
        }
    },
    yAxis: {
        type: 'value'
    },
    series:{
        data: yAxis,
        type: 'bar',
        barWidth: 20
    }
}
				// 
				pingfenEchart1.setOption(option);
				  //
				window.onresize = function() {
				    pingfenEchart1.resize();
				};
			})
		})
	}
	// 2
    const echartActive = ref('1')
    const echartTabClick = () =>{
		if(echartActive.value==1){
			echartClick1()
		}
		else if(echartActive.value==2){
			echartClick2()
		}
	}
	const echartClick2 = ()=>{
		echartActive.value = '2'
		echartVisible.value = true
		nextTick(()=>{
			var zhanghaoEchart2 = echarts.init(document.getElementById("zhanghaoEchart2"),'theme');
			context.$http({
				url: `${tableName}/group/zhanghao`,
				method: 'get'
			}).then(res=>{
				let obj = res.data.data
				let xAxis = [];
				let yAxis = [];
				let dataList = []
				for(let i=0;i<obj.length;i++){
				    xAxis.push(obj[i].zhanghao);
				    yAxis.push(parseFloat((obj[i].total)));
                    dataList.push({
				        value: parseFloat((obj[i].total)),
				        name: obj[i].zhanghao
				    })
				}
				var option = {};
				option = {
    title:{
        text: '',
        left: 'center'
    },
    legend: {
        orient: 'vertical',
        left: 'left'
    },
    tooltip: {
        trigger: 'item',
        formatter: '{b} : {c} ({d}%)'
    },
    series: [
        {
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: dataList,
            emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
            }
        }
    ]
}
				// 
				zhanghaoEchart2.setOption(option);
				  //
				window.onresize = function() {
				    zhanghaoEchart2.resize();
				};
			})
		})
	}
	import jiankangjianyiFormModel from '@/views/jiankangjianyi/formModel'
	const jiankangjianyiFormModelRef = ref(null)
    const jiankangjianyiCrossAddOrUpdateHandler = (row,type,crossOptAudit,crossOptPay,statusColumnName,tips,statusColumnValue) => {
		if(statusColumnName!=''&&!statusColumnName.startsWith("[")) {
			var obj = row
			for (var o in obj){
				if(o==statusColumnName && obj[o]==statusColumnValue){
					context?.$toolUtil.message(tips,'error')
					return;
				}
			}
		}
		nextTick(()=>{
			jiankangjianyiFormModelRef.value.init(row.id,'cross','',row,'ceshipingfen',statusColumnName,tips,statusColumnValue)
		})
    }
	//
	const init = () => {
		getList()
	}
	init()
</script>
<style lang="scss" scoped>

	// 
	.list_search_view {
		// 
		.search_form {
			// 
			.search_view {
				// label
				.search_label {
				}
				// item
				.search_box {
					// 
					:deep(.search_sel) {
						//
						.select-trigger{
							height: 100%;
							.el-input{
								height: 100%;
							}
						}
					}
				}
			}
			// 
			.search_btn_view {
				// 
				.search_btn {
				}
				// -
				.search_btn:hover {
				}
			}
		}
		//
		.btn_view {
			// 
			:deep(.el-button--default){
			}
			// -
			:deep(.el-button--default:hover){
			}
			// 
			:deep(.el-button--success){
			}
			// -
			:deep(.el-button--success:hover){
			}
			// 
			:deep(.el-button--danger){
			}
			// -
			:deep(.el-button--danger:hover){
			}
			// 
			:deep(.el-button--warning){
			}
			// -
			:deep(.el-button--warning:hover){
			}
		}
	}
	// 
	.el-table {
		:deep(.el-table__header-wrapper) {
			thead {
				tr {
					th {
						.cell {
						}
					}
				}
			}
		}
		:deep(.el-table__body-wrapper) {
			tbody {
				tr {
					td {
						.cell {
							// 
							.el-button--primary {
							}
							// -
							.el-button--primary:hover {
							}
							// 
							.el-button--info {
							}
							// -
							.el-button--info:hover {
							}
							// 
							.el-button--danger {
							}
							// -
							.el-button--danger:hover {
							}
							// 
							.el-button--success {
							}
							// -
							.el-button--success:hover {
							}
							// 
							.el-button--warning {
							}
							// -
							.el-button--warning:hover {
							}
						}
					}
				}
				tr:hover {
					td {
					}
				}
			}
		}
	}
	// 
	.el-pagination {
		// 
		:deep(.el-pagination__total) {
		}
		// 
		:deep(.btn-prev) {
		}
		// 
		:deep(.btn-next) {
		}
		// 
		:deep(.btn-prev:disabled) {
		}
		// 
		:deep(.btn-next:disabled) {
		}
		// 
		:deep(.el-pager) {
			// 
			.number {
			}
			// 
			.number:hover {
			}
			// 
			.number.is-active {
			}
		}
		// sizes
		:deep(.el-pagination__sizes) {
			display: inline-block;
			vertical-align: top;
			font-size: 13px;
			line-height: 28px;
			height: 28px;
			.el-select {
			}
		}
		// 
		:deep(.el-pagination__jump) {
			// 
			.el-input {
			}
		}
	}
</style>
