<template>
	<div>
		<div class="center_view">
			<div v-if="forumChild">
				<el-button type="success" @click="searchClick()"></el-button>
			</div>
			<div class="list_search_view">
				<el-form :model="searchQuery" class="search_form" v-if="!forumChild">
					<div class="search_view">
						<div class="search_label">
							
						</div>
						<div class="search_box">
							<el-input class="search_inp" v-model="searchQuery.title" placeholder=""
								clearable>
							</el-input>
						</div>
					</div>
					<div class="search_view">
						<div class="search_label">
							
						</div>
						<div class="search_box">
							<el-input class="search_inp" v-model="searchQuery.content" placeholder=""
								clearable>
							</el-input>
						</div>
					</div>
					<div class="search_btn_view">
						<el-button class="search_btn" type="primary" @click="searchClick()" size="small"></el-button>
					</div>
				</el-form>
				<div class="btn_view">
					<el-button class="add_btn" type="success" @click="addClick" v-if="btnAuth('forum','')">
						
					</el-button>
					<el-button class="del_btn" type="danger" :disabled="selRows.length?false:true" @click="delClick(null)"  v-if="btnAuth('forum','')">
						
					</el-button>
                    <el-button class="statis_btn" type="warning" @click="echartClick1" v-if="btnAuth('forum','')">
                        
                    </el-button>
				</div>
			</div>
			<el-table
				v-loading="listLoading"
				border
				:stripe='false'
				@selection-change="handleSelectionChange"
				ref="table"
				v-if="btnAuth('forum','')"
				:data="list"
				@row-click="listChange">
				<el-table-column :resizable='true' align="left" header-align="left" type="selection" width="55" />
				<el-table-column label="" width="70" :resizable='true' align="left" header-align="left">
					<template #default="scope">{{ (listQuery.page-1)*listQuery.limit+scope.$index + 1}}</template>
				</el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="title"
					v-if="!forumChild"
					label="">
					<template #default="scope">
						{{scope.row.title}}
					</template>
				</el-table-column>
                <el-table-column label="" v-if="forumChild" min-width="140" :resizable='true' :sortable='true' align="left" header-align="left">
                    <template #default="scope">
                        <span v-html="scope.row.content"></span>
                    </template>
                </el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="username"
					label="">
					<template #default="scope">
						{{scope.row.username}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="isdone"
					v-if="!forumChild"
					label="">
					<template #default="scope">
						{{scope.row.isdone}}
					</template>
				</el-table-column>
                <el-table-column v-if="btnAuth('forum','') && !forumChild" prop="isTop" label="" :resizable='true' :sortable='true' align="left" header-align="left">
                    <template #default="scope">
                        <el-switch :value="scope.row.isTop"
                                   :active-value="1"
                                   :inactive-value="0"
                                   @change="setTop(scope.row)">
                        </el-switch>
                    </template>
                </el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="topTime"
					v-if="!forumChild"
					label="">
					<template #default="scope">
						{{scope.row.topTime}}
					</template>
				</el-table-column>
				<el-table-column min-width="140"
					:resizable='true'
					:sortable='true'
					align="left"
					header-align="left"
					prop="thumbsupNumber"
					v-if="!forumChild"
					label="">
					<template #default="scope">
						{{scope.row.thumbsupNumber}}
					</template>
				</el-table-column>
				<el-table-column label="" width="300" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						<el-button class="view_btn" type="info" v-if=" btnAuth('forum','')" @click="infoClick(scope.row.id)">
							
						</el-button>
						<el-button class="edit_btn" type="primary" @click="editClick(scope.row.id)" v-if="!forumChild &&  btnAuth('forum','')">
													</el-button>
						<el-button class="del_btn" type="danger" @click="delClick(scope.row.id)"  v-if="btnAuth('forum','')">
													</el-button>
						<el-button class="operate_btn" v-if="!forumChild" type="warning" @click="searchClick(scope.row.id)">
							
						</el-button>
					</template>
				</el-table-column>
			</el-table>
			<el-pagination
				background
				:layout="layouts.join(',')"
				:total="total"
				:page-size="listQuery.limit"
                v-model:current-page="listQuery.page"
				prev-text="<"
				next-text=">"
				:hide-on-single-page="false"
				:style='{}'
				:page-sizes="[10, 20, 30, 40, 50, 100]"
				@size-change="sizeChange"
				@current-change="currentChange"  />
		</div>
		<formModel ref="formRef" @formModelChange="formModelChange"></formModel>
		<!--  -->
		<el-dialog v-model="echartVisible" title="" width="70%">
            <div  id="thumbsupNumberEchart1" style="width:100%;height:600px;"></div>
			<template #footer>
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="echartVisible=false"></el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import axios from 'axios'
    import moment from "moment"
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		onMounted,
		watch,
		computed,
		inject
	} from 'vue'
	import {
		useRoute,
		useRouter
	} from 'vue-router'
	import {
		ElMessageBox
	} from 'element-plus'
	import {
		useStore
	} from 'vuex';
	const store = useStore()
	const user = computed(()=>store.getters['user/session'])
	const avatar = ref(store.state.user.avatar)
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	import formModel from './formModel.vue'
	//

	const tableName = 'forum'
	const formName = ''
	const route = useRoute()
	//
	onMounted(()=>{
	})
	//
	const list = ref(null)
	const table = ref(null)
	const listQuery = ref({
		page: 1,
		limit: 20,
		sort: 'id',
		order: 'desc'
	})
	const searchQuery = ref({})
	const selRows = ref([])
	const listLoading = ref(false)
	const listChange = (row) =>{
		nextTick(()=>{
			//table.value.clearSelection()
			table.value.toggleRowSelection(row)
		})
	}
	const getList = (id=null)=>{
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		params['sort'] = 'id'
		params['parentid'] = 0
		if(id){
			params['parentid'] = id
		}
		if(searchQuery.value.title&&searchQuery.value.title!=''){
			params['title'] = '%' + searchQuery.value.title + '%'
		}
		context.$http({
			url: `forum/page`,
			method: 'get',
			params: params
		}).then(res=>{
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
			listLoading.value = false
		})
	}
	const delClick = (id) => {
		let ids = ref([])
		if (id) {
			ids.value = [id]
		} else {
			if (selRows.value.length) {
				for (let x in selRows.value) {
					ids.value.push(selRows.value[x].id)
				}
			} else {
				return false
			}
		}
		ElMessageBox.confirm(`${formName}`, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			context.$http({
				url: `${tableName}/delete`,
				method: 'post',
				data: ids.value
			}).then(res => {
				context?.$toolUtil.message('', 'success',()=>{
					getList()
				})
			})
		}).catch(_ => {})
	}
	//
	const handleSelectionChange = (e) => {
		selRows.value = e
	}
	//
	//
	const total = ref(0)
	const layouts = ref(["total","prev","pager","next","sizes","jumper"])
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	//
	//
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	//
	const forumChild = ref(false)
	const searchClick = (id) => {
		if(id){
			forumChild.value = true;
		}else{
			forumChild.value = false
		}
		listQuery.value.page = 1
		getList(id)
	}
	//
	const formRef = ref(null)
	const formModelChange=()=>{
		searchClick()
	}
	const addClick = ()=>{
		formRef.value.init()
	}
	const editClick = (id=null)=>{
		if(id){
			formRef.value.init(id,'edit')
			return
		}
		if(selRows.value.length){
			formRef.value.init(selRows.value[0].id,'edit')
		}
	}

	const infoClick = (id=null)=>{
		if(id){
			formRef.value.init(id,'info')
		}
		else if(selRows.value.length){
			formRef.value.init(selRows.value[0].id,'info')
		}
	}
	// 
	// 
	const preClick = (file) =>{
		if(!file){
			context?.$toolUtil.message('','error')
		}
		window.open(context?.$config.url + file)
	}
	// 
	const download = (file) => {
		if(!file){
			context?.$toolUtil.message('','error')
		}
		let arr = file.replace(new RegExp('file/', "g"), "")
		axios.get((location.href.split(context?.$config.name).length>1 ? location.href.split(context?.$config.name)[0] :'') + context?.$config.name + '/file/download?fileName=' + arr, {
			headers: {
				token: context?.$toolUtil.storageGet('Token')
			},
			responseType: "blob"
		}).then(({
			data
		}) => {
			const binaryData = [];
			binaryData.push(data);
			const objectUrl = window.URL.createObjectURL(new Blob(binaryData, {
				type: 'application/pdf;chartset=UTF-8'
			}))
			const a = document.createElement('a')
			a.href = objectUrl
			a.download = arr
			// a.click()
			// 
			a.dispatchEvent(new MouseEvent('click', {
				bubbles: true,
				cancelable: true,
				view: window
			}))
			window.URL.revokeObjectURL(data)
		})
	}
    import '@/assets/js/echarts-theme'
	// 1
	const echarts = inject("echarts")
	const echartVisible = ref(false)
	const echartClick1 = ()=>{
		echartVisible.value = true
		nextTick(()=>{
			var thumbsupNumberEchart1 = echarts.init(document.getElementById("thumbsupNumberEchart1"),'theme');
			context.$http({
				url: `${tableName}/value/title/thumbsupNumber?order=desc`,
				method: 'get'
			}).then(res=>{
				let obj = res.data.data
				let xAxis = [];
				let yAxis = [];
				let dataList = []
				for(let i=0;i<obj.length;i++){
				    xAxis.push(obj[i].title);
				    yAxis.push(parseFloat((obj[i].total)));
                    dataList.push({
				        value: parseFloat((obj[i].total)),
				        name: obj[i].title
				    })
				}
				var option = {};
                option = {
    title: {
        text: '',
        left: 'center'
    },
    tooltip: {
        trigger: 'item',
        formatter: '{b} : {c}'
    },
    xAxis: {
        data: xAxis,
        type: 'category',
    },
    yAxis: {
        type: 'value'
    },
    series:{
        data: yAxis,
        type: 'line'
    }
}
				// 
				thumbsupNumberEchart1.setOption(option);
				  //
				window.onresize = function() {
				    thumbsupNumberEchart1.resize();
				};
			})
		})
	}
    const setTop = (row)=>{
        let o_isTop = row.isTop
        if(row.isTop==0){
            row.topTime = row.addtime
            row.isTop = 1
        }else{
            row.topTime = context.$toolUtil.getCurDateTime()
            row.isTop = 0
        }
        context.$http.post(`${tableName}/update`,row).then(res=>{
            if(res.data.code==0){
                if(o_isTop==0||o_isTop==1){
                    context.$message.success("")
                }
                searchClick()
            }
        })
    }
	//
	const init = () => {
		getList()
	}
	init()
</script>
<style lang="scss" scoped>

	// 
	.list_search_view {
		// 
		.search_form {
			// 
			.search_view {
				// label
				.search_label {
				}
				// item
				.search_box {
					// 
					:deep(.search_inp) {
					}
				}
			}
			// 
			.search_btn_view {
				// 
				.search_btn {
				}
				// -
				.search_btn:hover {
				}
			}
		}
		//
		.btn_view {
			// 
			:deep(.el-button--default){
			}
			// -
			:deep(.el-button--default:hover){
			}
			// 
			:deep(.el-button--success){
			}
			// -
			:deep(.el-button--success:hover){
			}
			// 
			:deep(.el-button--danger){
			}
			// -
			:deep(.el-button--danger:hover){
			}
			// 
			:deep(.el-button--warning){
			}
			// -
			:deep(.el-button--warning:hover){
			}
		}
	}
	// 
	.el-table {
		:deep(.el-table__header-wrapper) {
			thead {
				tr {
					th {
						.cell {
						}
					}
				}
			}
		}
		:deep(.el-table__body-wrapper) {
			tbody {
				tr {
					td {
						.cell {
							// 
							.el-button--primary {
							}
							// -
							.el-button--primary:hover {
							}
							// 
							.el-button--info {
							}
							// -
							.el-button--info:hover {
							}
							// 
							.el-button--danger {
							}
							// -
							.el-button--danger:hover {
							}
							// 
							.el-button--success {
							}
							// -
							.el-button--success:hover {
							}
							// 
							.el-button--warning {
							}
							// -
							.el-button--warning:hover {
							}
						}
					}
				}
				tr:hover {
					td {
					}
				}
			}
		}
	}
	// 
	.el-pagination {
		// 
		:deep(.el-pagination__total) {
		}
		// 
		:deep(.btn-prev) {
		}
		// 
		:deep(.btn-next) {
		}
		// 
		:deep(.btn-prev:disabled) {
		}
		// 
		:deep(.btn-next:disabled) {
		}
		// 
		:deep(.el-pager) {
			// 
			.number {
			}
			// 
			.number:hover {
			}
			// 
			.number.is-active {
			}
		}
		// sizes
		:deep(.el-pagination__sizes) {
			display: inline-block;
			vertical-align: top;
			font-size: 13px;
			line-height: 28px;
			height: 28px;
			.el-select {
			}
		}
		// 
		:deep(.el-pagination__jump) {
			// 
			.el-input {
			}
		}
	}
</style>
