<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row >
					<el-col :span="12">
						<el-form-item label="" prop="total">
							<el-input class="list_inp" v-model.number="form.total" placeholder=""
                                type="number"
								:readonly="!isAdd||disabledForm.total?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="discounttotal">
							<el-input class="list_inp" v-model.number="form.discounttotal" placeholder=""
                                type="number"
								:readonly="!isAdd||disabledForm.discounttotal?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="type">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.type?true:false"
								v-model="form.type" 
								placeholder=""
								>
								<el-option v-for="(item,index) in typeLists" :label="item"
									:value="index + 1+''"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="status">
							<el-input class="list_inp" v-model="form.status" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.status?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="address">
							<el-input class="list_inp" v-model="form.address" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.address?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="tel">
							<el-input class="list_inp" v-model="form.tel" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.tel?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="consignee">
							<el-input class="list_inp" v-model="form.consignee" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.consignee?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="remark">
							<el-input class="list_inp" v-model="form.remark" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.remark?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="addtime">
							<el-date-picker
								class="list_date"
								v-model="form.addtime"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.addtime?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="orderid">
							<el-input class="list_inp" v-model="form.orderid" :readonly="true" placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="goodname">
							<el-input class="list_inp" v-model="form.goodname" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.goodname?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item prop="picture"
									  label=""
						>
							<uploads
								:disabled="!isAdd||disabledForm.picture?true:false"
								action="file/upload"
								tip=""
								style="width: 100%;text-align: left;"
								:fileUrls="form.picture?form.picture:''" 
								@change="pictureUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="buynumber">
							<el-input class="list_inp" v-model.number="form.buynumber" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.buynumber?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="price">
							<el-input class="list_inp" v-model.number="form.price" placeholder=""
                                type="number"
								:readonly="!isAdd||disabledForm.price?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="discountprice">
							<el-input class="list_inp" v-model.number="form.discountprice" placeholder=""
                                type="number"
								:readonly="!isAdd||disabledForm.discountprice?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="goodtype">
							<el-input class="list_inp" v-model="form.goodtype" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.goodtype?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="addtime">
							<el-input class="list_inp" v-model="form.addtime" placeholder=""
							readonly />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="logistics">
							<editor :value="form.logistics" placeholder="" :readonly="type == 'logistics'?false:true"
								class="list_editor" @change="(e)=>editorChange(e,'logistics')"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick"></el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'orders'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
        total : false,
        discounttotal : false,
        type : false,
        status : false,
        address : false,
        tel : false,
        consignee : false,
        remark : false,
        logistics : false,
        role : false,
        addtime : false,
        userid : false,
        orderid : false,
        tablename : false,
        goodid : false,
        goodname : false,
        picture : false,
        buynumber : false,
        price : false,
        discountprice : false,
        goodtype : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
    
	const rules = ref({
		total: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		discounttotal: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		type: [
		],
		status: [
		],
		address: [
		],
		tel: [
		],
		consignee: [
		],
		remark: [
		],
		logistics: [
		],
		role: [
		],
		addtime: [
		],
		userid: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		orderid: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		tablename: [
		],
		goodid: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		goodname: [
		],
		picture: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		buynumber: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		price: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		discountprice: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		goodtype: [
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
    const typeLists = ref(['','','',''])
	//
	const pictureUploadSuccess=(e)=>{
		form.value.picture = e
	}

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			total: '',
			discounttotal: '',
			type: '',
			status: '',
			address: '',
			tel: '',
			consignee: '',
			remark: '',
			logistics: '',
			role: '',
			addtime: '',
			userid: '',
			orderid: getUUID(),
			goodid: '',
			goodname: '',
			picture: '',
			buynumber: '',
			price: '',
			discountprice: '',
			goodtype: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.logistics = res.data.data.logistics?(res.data.data.logistics.replace(reg,'../../../pdas/file')):'';
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'logistics'){
			isAdd.value = false
			formTitle.value = ''
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='total'){
					form.value.total = row[x];
					disabledForm.value.total = true;
					continue;
				}
				if(x=='discounttotal'){
					form.value.discounttotal = row[x];
					disabledForm.value.discounttotal = true;
					continue;
				}
				if(x=='type'){
					form.value.type = row[x];
					disabledForm.value.type = true;
					continue;
				}
				if(x=='status'){
					form.value.status = row[x];
					disabledForm.value.status = true;
					continue;
				}
				if(x=='address'){
					form.value.address = row[x];
					disabledForm.value.address = true;
					continue;
				}
				if(x=='tel'){
					form.value.tel = row[x];
					disabledForm.value.tel = true;
					continue;
				}
				if(x=='consignee'){
					form.value.consignee = row[x];
					disabledForm.value.consignee = true;
					continue;
				}
				if(x=='remark'){
					form.value.remark = row[x];
					disabledForm.value.remark = true;
					continue;
				}
				if(x=='logistics'){
					form.value.logistics = row[x];
					disabledForm.value.logistics = true;
					continue;
				}
				if(x=='role'){
					form.value.role = row[x];
					disabledForm.value.role = true;
					continue;
				}
				if(x=='addtime'){
					form.value.addtime = row[x];
					disabledForm.value.addtime = true;
					continue;
				}
				if(x=='userid'){
					form.value.userid = row[x];
					disabledForm.value.userid = true;
					continue;
				}
				if(x=='orderid'){
					form.value.orderid = row[x];
					disabledForm.value.orderid = true;
					continue;
				}
				if(x=='tablename'){
					form.value.tablename = row[x];
					disabledForm.value.tablename = true;
					continue;
				}
				if(x=='goodid'){
					form.value.goodid = row[x];
					disabledForm.value.goodid = true;
					continue;
				}
				if(x=='goodname'){
					form.value.goodname = row[x];
					disabledForm.value.goodname = true;
					continue;
				}
				if(x=='picture'){
					form.value.picture = row[x];
					disabledForm.value.picture = true;
					continue;
				}
				if(x=='buynumber'){
					form.value.buynumber = row[x];
					disabledForm.value.buynumber = true;
					continue;
				}
				if(x=='price'){
					form.value.price = row[x];
					disabledForm.value.price = true;
					continue;
				}
				if(x=='discountprice'){
					form.value.discountprice = row[x];
					disabledForm.value.discountprice = true;
					continue;
				}
				if(x=='goodtype'){
					form.value.goodtype = row[x];
					disabledForm.value.goodtype = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		typeLists.value = ",".split(',')
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save= async ()=>{
		if(form.value.picture!=null) {
			form.value.picture = form.value.picture.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(``,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(``,'success')
                        formVisible.value = false
					})
				}
			}else{
                context.$message.error('')
            }
		})
	}
	//
	const changeCrossData = async (row)=>{
        if(type.value == 'cross'){
            await context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {
				}
				//
				.list_date {
				}
				// 
				.list_sel {
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
				// 
				.list_editor {
				}
				//
				.el-upload-list  {
					//
					.el-upload__tip {
					}
					//
					.el-upload--picture-card {
						//
						.el-icon{
						}
					}
					.el-upload-list__item {
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
