<template>
	<div>
		<div class="center_view">
			<div>
				<div class="list_search_view">
					<el-form :model="searchQuery" class="search_form">
						<div class="search_view">
							<div class="search_label">
								
							</div>
							<div class="search_box">
								<el-input class="search_inp" v-model="searchQuery.papername" placeholder=""
									 clearable>
								</el-input>
							</div>
						</div>
						<div class="search_view">
							<div class="search_label">
								
							</div>
							<div class="search_box">
								<el-input class="search_inp" v-model="searchQuery.questionname" placeholder=""
									 clearable>
								</el-input>
							</div>
						</div>
						<div class="search_btn_view">
							<el-button class="search_btn" type="primary" @click="searchClick()"></el-button>
						</div>
					</el-form>
					<br>
					<div class="btn_view">
						<el-button class="add_btn" type="success" @click="addClick"
							v-if="btnAuth('examquestion','')"></el-button>
						<el-button class="del_btn" type="danger" :disabled="selRows.length?false:true"
							@click="delClick(null)" v-if="btnAuth('examquestion','')"></el-button>
						<el-button class="other_btn" type="default" @click="exportClick" v-if="btnAuth('examquestion','')"></el-button>
						<el-button class="other_btn" type="default" @click="printClick" v-if="btnAuth('examquestion','')"></el-button>
					</div>
				</div>
			</div>
			<br>
			<el-table v-loading="listLoading" border :stripe='false' @selection-change="handleSelectionChange" ref="table"
				v-if="btnAuth('exampaper','')" :data="list" @row-click="listChange">
				<el-table-column :resizable='true' align="left" header-align="left" type="selection" width="55" />
				<el-table-column label="" width="120" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">{{ scope.$index + 1}}</template>
				</el-table-column>
				<el-table-column label="" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						{{scope.row.papername}}
					</template>
				</el-table-column>
				<el-table-column label="" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						{{scope.row.questionname}}
					</template>
				</el-table-column>
				<el-table-column label="" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						<div v-for="(item,index) in scope.row.optionList">
							<el-tag type="success" v-if="index==0" style="margin:0 0 4px">{{item.text}}</el-tag>
							<el-tag type="warning" v-if="index==1" style="margin:0 0 4px">{{item.text}}</el-tag>
							<el-tag type="danger" v-if="index==2" style="margin:0 0 4px">{{item.text}}</el-tag>
							<el-tag type="info" v-if="index==3" style="margin:0 0 4px">{{item.text}}</el-tag>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						<el-button class="view_btn" :type="'info'" @click="infoClick(scope.row.id)" size="small"
							v-if="btnAuth('examquestion','')"></el-button>
						<el-button class="edit_btn" :type="'primary'" @click="editClick(scope.row.id)" size="small"
							v-if="btnAuth('examquestion','')"></el-button>
						<el-button class="del_btn" :type="'danger'" @click="delClick(scope.row.id)" size="small"
							v-if="btnAuth('examquestion','')"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<el-pagination
				background
				:layout="layouts.join(',')"
				:total="total" 
				:page-size="listQuery.limit"
                v-model:current-page="listQuery.page"
				prev-text="<"
				next-text=">"
				:hide-on-single-page="false"
				:style='{}'
				@size-change="sizeChange"
				@current-change="currentChange"  />
		</div>
		<formModel ref="formRef" @formModelChange="formModelChange"></formModel>
	</div>
</template>

<script setup>
	import {
		ref,
		nextTick,
		getCurrentInstance
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router'
	import {
		ElMessageBox
	} from 'element-plus'
	import formModel from './formModel.vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	//
	const tableName = 'examquestion'
	const formName = ''
	const route = useRoute()
	//
	const listLoading = ref(false)
	const list = ref([])
	//
	const total = ref(0)
	const layouts = ref(["total","prev","pager","next","sizes","jumper"])
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	//
	//
	const btnAuth = (e, a) => {
		return context?.$toolUtil.isAuth(e, a)
	}
	//
	const searchClick = () => {
		listQuery.value.page = 1
		getList()
	}
	const table = ref(null)
	const listChange = (row) => {
		nextTick(() => {
			table.value.clearSelection()
			table.value.toggleRowSelection(row)
		})
	}
	const listQuery = ref({
		page: 1,
		limit: 20,
		sort: 'id',
		order: 'desc'
	})
	const searchQuery = ref({})
	const selRows = ref([])
	//
	const handleSelectionChange = (e) => {
		selRows.value = e
	}
	//
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		params['sort'] = 'id'
		params['order'] = 'desc'
		if (searchQuery.value.papername && searchQuery.value.papername != '') {
			params['papername'] = '%' + searchQuery.value.papername + '%'
		}
		if (searchQuery.value.questionname && searchQuery.value.questionname != '') {
			params['questionname'] = '%' + searchQuery.value.questionname + '%'
		}
		context?.$http({
			url: `${tableName}/page`,
			method: 'get',
			params: params
		}).then(res => {
			for(let x in res.data.data.list) {
				if(res.data.data.list[x].options) {
					res.data.data.list[x].optionList = JSON.parse(res.data.data.list[x].options)
				}else{
					res.data.data.list[x].optionList = []
				}
			}
			listLoading.value = false
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
		})
	}
	const formRef = ref(null)
	const formModelChange=()=>{
		searchClick()
	}
	const addClick = ()=>{
		formRef.value.init('','')
	}
	const editClick = (id=null)=>{
		if(id){
			formRef.value.init(id,'')
		}
	}
	const infoClick = (id=null)=>{
		if(id){
			formRef.value.init(id,'')
		}
	}
	//
	const exportClick = () => {
		import('@/utils/Export2Excel').then(excel => {
			const tHeader = [
				"",
				"",
				"",
				"",
				"",
				"",
				"",
			]
			const filterVal = [
				"papername",
				"questionname",
				"typeName",
				"options",
				"score",
				"answer",
				"analysis",
			]
			excel.export_json_to_excel2(tHeader, list.value, filterVal, '')
		})
	}
	//
	const printClick = () => {
		let data = []
		for (let x in list.value) {
			data.push({
				papername: list.value[x].papername,
				questionname: list.value[x].questionname,
				typeName: list.value[x].typeName,
				options: list.value[x].options,
				score: list.value[x].score,
				answer: list.value[x].answer,
				analysis: list.value[x].analysis,
			})
		}
		printJS({
			printable: data,
			properties: [{
					field: 'papername',
					displayName: '',
					columnSize: 1
				},
				{
					field: 'questionname',
					displayName: '',
					columnSize: 1
				},
				{
					field: 'typeName',
					displayName: '',
					columnSize: 1
				},
				{
					field: 'options',
					displayName: '',
					columnSize: 1
				},
				{
					field: 'score',
					displayName: '',
					columnSize: 1
				},
				{
					field: 'answer',
					displayName: '',
					columnSize: 1
				},
				{
					field: 'analysis',
					displayName: '',
					columnSize: 1
				},
			],
			type: 'json',
			header: '',
			gridStyle: 'border: 1px solid #3971A5; text-align: center; padding: 6px 10px',
			gridHeaderStyle: 'color: #000; border: 1px solid #3971A5; padding: 6px 10px'
		})
	}
	//
	const delClick = (id) => {
		let ids = ref([])
		if (id) {
			ids.value = [id]
		} else {
			if (selRows.value.length) {
				for (let x in selRows.value) {
					ids.value.push(selRows.value[x].id)
				}
			} else {
				return false
			}
		}
		ElMessageBox.confirm(`${formName}`, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning',
		}).then(() => {
			context?.$http({
				url: `${tableName}/delete`,
				method: 'post',
				data: ids.value
			}).then(res => {
				context.$message.success('')
                getList()
			})
		}).catch(_ => {})
	}
	const init = () => {
		getList()
	}
	init()
</script>

<style lang="scss" scoped>
	// 
	.list_search_view {
		// 
		.search_form {
			// 
			.search_view {
				// label
				.search_label {
				}
				// item
				.search_box {
					// 
					:deep(.search_inp) {
					}
				}
			}
			// 
			.search_btn_view {
				width: 20%;
				display: flex;
				padding: 0 20px;

				// 
				.search_btn {
				}
				// -
				.search_btn:hover {
				}
			}
		}
		//
		.btn_view {
			// 
			:deep(.el-button--success) {
			}
			// -
			:deep(.el-button--success:hover) {
			}
			// 
			:deep(.el-button--danger) {
			}
			// -
			:deep(.el-button--danger:hover) {
			}
			// 
			:deep(.el-button--warning) {
			}
			// -
			:deep(.el-button--warning:hover) {
			}
		}
	}
	// 
	.el-table {
		:deep(.el-table__header-wrapper) {
			thead {
				tr {
					th {
						.cell {
						}
					}
				}
			}
		}
		:deep(.el-table__body-wrapper) {
			tbody {
				tr {
					td {
						.cell {
							// 
							.el-button--primary {
							}
							// -
							.el-button--primary:hover {
							}
							// 
							.el-button--info {
							}
							// -
							.el-button--info:hover {
							}
							// 
							.el-button--danger {
							}
							// -
							.el-button--danger:hover {
							}
						}
					}
				}
				tr:hover {
					td {
					}
				}
			}
		}
	}
	// 
	.el-pagination {
		// 
		:deep(.el-pagination__total) {
		}
		// 
		:deep(.btn-prev) {
		}
		// 
		:deep(.btn-next) {
		}
		// 
		:deep(.btn-prev:disabled) {
		}
		// 
		:deep(.btn-next:disabled) {
		}
		// 
		:deep(.el-pager) {
			// 
			.number {
			}
			// 
			.number:hover {
			}
			// 
			.number.active {
			}
		}
		// sizes
		:deep(.el-pagination__sizes) {
			display: inline-block;
			vertical-align: top;
			font-size: 13px;
			line-height: 28px;
			height: 28px;
			.el-select {
			}
		}
		// 
		:deep(.el-pagination__jump) {
			// 
			.el-input {
			}
		}
	}
</style>