<template>
	<div class="center_view">
		<div class="list_search_view">
			<el-form :inline="true" :model="searchForm" class="search_form">
				<div class="search_view">
					<div class="search_label"></div>
					<div class="search_box">
						<el-input v-model="searchForm.zhanghao" placeholder="" clearable></el-input>
					</div>
				</div>
				<div class="search_view">
					<div class="search_label"></div>
					<div class="search_box">
						<el-input v-model="searchForm.xingming" placeholder="" clearable></el-input>
					</div>
				</div>
				<div class="search_btn_view">
					<el-button type="primary" @click="getList"></el-button>
					<el-button @click="resetForm"></el-button>
					<el-button type="primary" @click="addOrUpdateHandler()"></el-button>
					<el-button type="danger" @click="deleteHandler()"></el-button>
				</div>
			</el-form>
		</div>
		<div class="list_table_view">
			<el-table :data="dataList" border v-loading="listLoading" @selection-change="selectionChangeHandler">
				<el-table-column type="selection" width="50" align="center"></el-table-column>
				<el-table-column prop="zhanghao" header-align="center" align="center" label=""></el-table-column>
				<el-table-column prop="xingming" header-align="center" align="center" label=""></el-table-column>
				<el-table-column prop="xingbie" header-align="center" align="center" label=""></el-table-column>
				<el-table-column prop="touxiang" header-align="center" align="center" label="">
					<template #default="scope">
						<div v-if="scope.row.touxiang">
							<img :src="$toolUtil.getImgUrl(scope.row.touxiang)" style="width: 100px;height: 100px;">
						</div>
						<div v-else></div>
					</template>
				</el-table-column>
				<el-table-column prop="shouji" header-align="center" align="center" label=""></el-table-column>
				<el-table-column prop="youxiang" header-align="center" align="center" label=""></el-table-column>
				<el-table-column prop="keshi" header-align="center" align="center" label=""></el-table-column>
				<el-table-column fixed="right" header-align="center" align="center" width="150" label="">
					<template #default="scope">
						<el-button type="text" size="small" @click="addOrUpdateHandler(scope.row.id)"></el-button>
						<el-button type="text" size="small" @click="deleteHandler(scope.row.id)"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<el-pagination
				@size-change="sizeChangeHandle"
				@current-change="currentChangeHandle"
				:current-page="pageIndex"
				:page-sizes="[10, 20, 50, 100]"
				:page-size="pageSize"
				:total="totalPage"
				layout="total, sizes, prev, pager, next, jumper"
				class="pagination">
			</el-pagination>
		</div>
		
		<el-dialog class="edit_form" v-model="addOrUpdateFlag" :title="!dataForm.id ? '' : ''" width="70%" destroy-on-close>
			<el-form :model="dataForm" :rules="dataFormRules" ref="dataFormRef" label-width="120px">
				<el-form-item label="" prop="zhanghao">
					<el-input v-model="dataForm.zhanghao" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="mima">
					<el-input v-model="dataForm.mima" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="xingming">
					<el-input v-model="dataForm.xingming" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="xingbie">
					<el-select v-model="dataForm.xingbie" placeholder="">
						<el-option label="" value=""></el-option>
						<el-option label="" value=""></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="touxiang">
					<upload v-model="dataForm.touxiang" type="img"></upload>
				</el-form-item>
				<el-form-item label="" prop="shouji">
					<el-input v-model="dataForm.shouji" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="youxiang">
					<el-input v-model="dataForm.youxiang" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="keshi">
					<el-input v-model="dataForm.keshi" placeholder=""></el-input>
				</el-form-item>
			</el-form>
			<template #footer>
				<div class="formModel_btn_box">
					<el-button @click="addOrUpdateFlag = false"></el-button>
					<el-button type="primary" @click="dataFormSubmit()"></el-button>
				</div>
			</template>
		</el-dialog>
	</div>
</template>

<script setup>
	import { ref, reactive, onMounted } from 'vue'
	import { ElMessage, ElMessageBox } from 'element-plus'
	import http from '@/utils/http'
	import config from '@/utils/config'
	import toolUtil from '@/utils/toolUtil'
	
	const searchForm = reactive({
		zhanghao: '',
		xingming: ''
	})
	
	const dataList = ref([])
	const pageIndex = ref(1)
	const pageSize = ref(10)
	const totalPage = ref(0)
	const listLoading = ref(false)
	const dataListSelections = ref([])
	
	const addOrUpdateFlag = ref(false)
	const dataForm = reactive({
		id: '',
		zhanghao: '',
		mima: '',
		xingming: '',
		xingbie: '',
		touxiang: '',
		shouji: '',
		youxiang: '',
		keshi: ''
	})
	
	const dataFormRef = ref(null)
	const dataFormRules = reactive({
		zhanghao: [{ required: true, message: '', trigger: 'blur' }],
		mima: [{ required: true, message: '', trigger: 'blur' }],
		xingming: [{ required: true, message: '', trigger: 'blur' }],
		xingbie: [{ required: true, message: '', trigger: 'blur' }],
		keshi: [{ required: true, message: '', trigger: 'blur' }]
	})
	
	onMounted(() => {
		getList()
	})
	
	// 
	const getList = () => {
		listLoading.value = true
		let params = {
			page: pageIndex.value,
			limit: pageSize.value,
			zhanghao: searchForm.zhanghao,
			xingming: searchForm.xingming
		}
		http.get('keshizhureng/page', {params}).then(({data}) => {
			if (data && data.code === 0) {
				dataList.value = data.data.list
				totalPage.value = data.data.total
			}
			listLoading.value = false
		})
	}
	
	// 
	const sizeChangeHandle = (val) => {
		pageSize.value = val
		pageIndex.value = 1
		getList()
	}
	
	// 
	const currentChangeHandle = (val) => {
		pageIndex.value = val
		getList()
	}
	
	// 
	const selectionChangeHandler = (selection) => {
		dataListSelections.value = selection
	}
	
	// 
	const resetForm = () => {
		searchForm.zhanghao = ''
		searchForm.xingming = ''
		getList()
	}
	
	// /
	const addOrUpdateHandler = (id) => {
		addOrUpdateFlag.value = true
		// 
		if (dataFormRef.value) {
			dataFormRef.value.resetFields()
		}
		
		if (id) {
			// 
			http.get(`keshizhureng/info/${id}`).then(({data}) => {
				if (data && data.code === 0) {
					Object.assign(dataForm, data.data)
				}
			})
		} else {
			// 
			Object.keys(dataForm).forEach(key => {
				dataForm[key] = ''
			})
		}
	}
	
	// 
	const dataFormSubmit = () => {
		dataFormRef.value.validate((valid) => {
			if (valid) {
				let url = dataForm.id ? 'keshizhureng/update' : 'keshizhureng/save'
				http.post(url, dataForm).then(({data}) => {
					if (data && data.code === 0) {
						ElMessage({
							message: '',
							type: 'success',
							duration: 1500,
							onClose: () => {
								addOrUpdateFlag.value = false
								getList()
							}
						})
					} else {
						ElMessage.error(data.msg)
					}
				})
			}
		})
	}
	
	// 
	const deleteHandler = (id) => {
		let ids = id ? [id] : dataListSelections.value.map(item => item.id)
		if (ids.length === 0) {
			ElMessage.error('')
			return
		}
		
		ElMessageBox.confirm('?', '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning'
		}).then(() => {
			http.post('keshizhureng/delete', ids).then(({data}) => {
				if (data && data.code === 0) {
					ElMessage({
						message: '',
						type: 'success',
						duration: 1500,
						onClose: () => {
							getList()
						}
					})
				} else {
					ElMessage.error(data.msg)
				}
			})
		})
	}
</script>

<style scoped>
.center_view {
	padding: 20px;
}
.list_search_view {
	margin-bottom: 20px;
}
.search_form {
	display: flex;
	flex-wrap: wrap;
}
.search_view {
	display: flex;
	align-items: center;
	margin-right: 20px;
	margin-bottom: 10px;
}
.search_label {
	width: 100px;
	text-align: right;
}
.search_box {
	width: 200px;
}
.search_btn_view {
	margin-left: 20px;
}
.list_table_view {
	margin-top: 20px;
}
.pagination {
	margin-top: 20px;
	text-align: right;
}
</style> 