<template>
  <div class="center_view">
    <div class="user_info">
      <div class="avatar">
        <img :src="$toolUtil.getImgUrl(user.touxiang)" v-if="user.touxiang">
        <img src="@/assets/img/avatar.png" v-else>
      </div>
      <div class="info">
        <div class="name">{{user.xingming}}</div>
        <div class="role"></div>
        <div class="keshi">{{user.keshi}}</div>
      </div>
    </div>
    
    <div class="info_card">
      <el-card class="box-card">
        <template #header>
          <div class="card-header">
            <span></span>
            <el-button class="button" type="text" @click="editInfo"></el-button>
          </div>
        </template>
        <div class="info_item">
          <span class="label"></span>
          <span class="value">{{user.zhanghao}}</span>
        </div>
        <div class="info_item">
          <span class="label"></span>
          <span class="value">{{user.xingming}}</span>
        </div>
        <div class="info_item">
          <span class="label"></span>
          <span class="value">{{user.xingbie}}</span>
        </div>
        <div class="info_item">
          <span class="label"></span>
          <span class="value">{{user.shouji}}</span>
        </div>
        <div class="info_item">
          <span class="label"></span>
          <span class="value">{{user.youxiang}}</span>
        </div>
        <div class="info_item">
          <span class="label"></span>
          <span class="value">{{user.keshi}}</span>
        </div>
      </el-card>
    </div>
    
    <el-dialog v-model="editDialogVisible" title="" width="50%">
      <el-form :model="editForm" :rules="editRules" ref="editFormRef" label-width="100px">
        <el-form-item label="" prop="zhanghao">
          <el-input v-model="editForm.zhanghao" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="xingming">
          <el-input v-model="editForm.xingming"></el-input>
        </el-form-item>
        <el-form-item label="" prop="xingbie">
          <el-select v-model="editForm.xingbie" placeholder="" style="width: 100%;">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="touxiang">
          <upload v-model="editForm.touxiang" type="img"></upload>
        </el-form-item>
        <el-form-item label="" prop="shouji">
          <el-input v-model="editForm.shouji"></el-input>
        </el-form-item>
        <el-form-item label="" prop="youxiang">
          <el-input v-model="editForm.youxiang"></el-input>
        </el-form-item>
        <el-form-item label="" prop="keshi">
          <el-input v-model="editForm.keshi"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mima">
          <el-input v-model="editForm.mima" type="password" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="editDialogVisible = false"></el-button>
          <el-button type="primary" @click="submitEdit"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue'
import { ElMessage } from 'element-plus'
import http from '@/utils/http'
import toolUtil from '@/utils/toolUtil'

const user = ref({})
const editDialogVisible = ref(false)
const editForm = reactive({
  id: '',
  zhanghao: '',
  mima: '',
  xingming: '',
  xingbie: '',
  touxiang: '',
  shouji: '',
  youxiang: '',
  keshi: ''
})
const editFormRef = ref(null)
const editRules = reactive({
  xingming: [{ required: true, message: '', trigger: 'blur' }],
  xingbie: [{ required: true, message: '', trigger: 'blur' }],
  keshi: [{ required: true, message: '', trigger: 'blur' }]
})

onMounted(() => {
  getUserInfo()
})

// 
const getUserInfo = () => {
  http.get('keshizhureng/session').then(({data}) => {
    if (data && data.code === 0) {
      user.value = data.data
    }
  })
}

// 
const editInfo = () => {
  editDialogVisible.value = true
  Object.assign(editForm, user.value)
  editForm.mima = '' // 
}

// 
const submitEdit = () => {
  editFormRef.value.validate((valid) => {
    if (valid) {
      http.post('keshizhureng/update', editForm).then(({data}) => {
        if (data && data.code === 0) {
          ElMessage({
            message: '',
            type: 'success',
            duration: 1500,
            onClose: () => {
              editDialogVisible.value = false
              getUserInfo()
            }
          })
        } else {
          ElMessage.error(data.msg)
        }
      })
    }
  })
}
</script>

