<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row >
					<el-col :span="12">
						<el-form-item label="" prop="ceshixiangmu">
							<el-input class="list_inp" v-model="form.ceshixiangmu" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.ceshixiangmu?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="ceshishijian">
							<el-date-picker
								class="list_date"
								v-model="form.ceshishijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.ceshishijian?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="ceshibaogao">
							<uploads
								:disabled="!isAdd||disabledForm.ceshibaogao?true:false"
								type="file"
								action="file/upload" 
								tip="" 
								:limit="1" 
								style="width: 100%;text-align: left;"
								:fileUrls="form.ceshibaogao?form.ceshibaogao:''" 
								@change="ceshibaogaoUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="ceshichengji">
							<el-input class="list_inp" v-model="form.ceshichengji" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.ceshichengji?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="shentijinengpinggu">
							<el-input class="list_inp" v-model="form.shentijinengpinggu" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.shentijinengpinggu?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="zhanghao">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.zhanghao?true:false"
								v-model="form.zhanghao" 
								placeholder=""
								@change="zhanghaoChange">
								<el-option v-for="(item,index) in zhanghaoLists" :label="item" :value="item">
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="xingming">
							<el-input class="list_inp" v-model="form.xingming" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.xingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="xingbie">
							<el-input class="list_inp" v-model="form.xingbie" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.xingbie?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="baogaoshijian">
							<el-date-picker
								class="list_date"
								v-model="form.baogaoshijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.baogaoshijian?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="zixunshizhanghao">
							<el-input class="list_inp" v-model="form.zixunshizhanghao" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.zixunshizhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="zixunshixingming">
							<el-input class="list_inp" v-model="form.zixunshixingming" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.zixunshixingming?true:false" />
						</el-form-item>
					</el-col>

				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick"></el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'ceshibaogao'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
        ceshixiangmu : false,
        ceshishijian : false,
        ceshibaogao : false,
        ceshichengji : false,
        shentijinengpinggu : false,
        zhanghao : false,
        xingming : false,
        xingbie : false,
        baogaoshijian : false,
        zixunshizhanghao : false,
        zixunshixingming : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
    
	const rules = ref({
		ceshixiangmu: [
		],
		ceshishijian: [
		],
		ceshibaogao: [
		],
		ceshichengji: [
		],
		shentijinengpinggu: [
		],
		zhanghao: [
		],
		xingming: [
		],
		xingbie: [
		],
		baogaoshijian: [
		],
		zixunshizhanghao: [
		],
		zixunshixingming: [
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
	const ceshibaogaoUploadSuccess=(e)=>{
		form.value.ceshibaogao = e
	}
	//
	const zhanghaoLists = ref([])

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			ceshixiangmu: '',
			ceshishijian: '',
			ceshibaogao: '',
			ceshichengji: '',
			shentijinengpinggu: '',
			zhanghao: '',
			xingming: '',
			xingbie: '',
			baogaoshijian: '',
			zixunshizhanghao: '',
			zixunshixingming: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.baogaoshijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='ceshixiangmu'){
					form.value.ceshixiangmu = row[x];
					disabledForm.value.ceshixiangmu = true;
					continue;
				}
				if(x=='ceshishijian'){
					form.value.ceshishijian = row[x];
					disabledForm.value.ceshishijian = true;
					continue;
				}
				if(x=='ceshibaogao'){
					form.value.ceshibaogao = row[x];
					disabledForm.value.ceshibaogao = true;
					continue;
				}
				if(x=='ceshichengji'){
					form.value.ceshichengji = row[x];
					disabledForm.value.ceshichengji = true;
					continue;
				}
				if(x=='shentijinengpinggu'){
					form.value.shentijinengpinggu = row[x];
					disabledForm.value.shentijinengpinggu = true;
					continue;
				}
				if(x=='zhanghao'){
					form.value.zhanghao = row[x];
					disabledForm.value.zhanghao = true;
					continue;
				}
				if(x=='xingming'){
					form.value.xingming = row[x];
					disabledForm.value.xingming = true;
					continue;
				}
				if(x=='xingbie'){
					form.value.xingbie = row[x];
					disabledForm.value.xingbie = true;
					continue;
				}
				if(x=='baogaoshijian'){
					form.value.baogaoshijian = row[x];
					disabledForm.value.baogaoshijian = true;
					continue;
				}
				if(x=='zixunshizhanghao'){
					form.value.zixunshizhanghao = row[x];
					disabledForm.value.zixunshizhanghao = true;
					continue;
				}
				if(x=='zixunshixingming'){
					form.value.zixunshixingming = row[x];
					disabledForm.value.zixunshixingming = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('zixunshizhanghao')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.zixunshizhanghao = json.zixunshizhanghao
				disabledForm.value.zixunshizhanghao = true;
			}
			if(json.hasOwnProperty('zixunshixingming')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.zixunshixingming = json.zixunshixingming
				disabledForm.value.zixunshixingming = true;
			}
		})
		context?.$http({
			url: `option/yonghu/zhanghao`,
			method: 'get'
		}).then(res=>{
			zhanghaoLists.value = res.data.data
		})
		//
		disabledForm.value.xingming = true;
		//
		disabledForm.value.xingbie = true;
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	const zhanghaoChange=()=>{
		context?.$http({
			url: `follow/yonghu/zhanghao?columnValue=` + form.value.zhanghao,
			method: 'get'
		}).then(res=>{
			if(res.data.data.xingming){
				form.value.xingming = res.data.data.xingming
			}
			if(res.data.data.xingbie){
				form.value.xingbie = res.data.data.xingbie
			}
		})
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save= async ()=>{
		if(form.value.ceshibaogao!=null) {
			form.value.ceshibaogao = form.value.ceshibaogao.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(``,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(``,'success')
                        formVisible.value = false
					})
				}
			}else{
                context.$message.error('')
            }
		})
	}
	//
	const changeCrossData = async (row)=>{
        if(type.value == 'cross'){
            await context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {
				}
				//
				.list_date {
				}
				// 
				.list_sel {
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
				//
				.upload-demo {
					width: 100%;
					//
					.el-upload-dragger {
					}
					//
					.el-icon--upload {
					}
					//
					.el-upload__text {
						em {
						}
					}
					//
					.el-upload__tip {
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
