<template>
	<div>
		<div class="center_view edit_form">
			<el-form class="userinfo_form" ref="userinfoFormRef" :model="form">
				<el-row>
					<el-col :span="12">
						<el-form-item label="" prop="zixunshizhanghao">
							<el-input class="list_inp" v-model="user.zixunshizhanghao" readonly placeholder="" clearable />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="zixunshixingming">
							<el-input class="list_inp" v-model="user.zixunshixingming"  placeholder="" clearable />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="xingbie">
							<el-select 
								class="list_sel" 
								v-model="user.xingbie" 
								placeholder=""
								>
								<el-option v-for="item in zixunshixingbieLists" :label="item" :value="item"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="touxiang">
							<uploads
								action="file/upload" 
								tip=""
								style="width: 100%;text-align: left;"
								:fileUrls="user.touxiang?user.touxiang:''" 
								@change="zixunshitouxiangUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="lianxifangshi">
							<el-input class="list_inp" v-model="user.lianxifangshi"  placeholder="" clearable />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="zizhirenzheng">
							<uploads
								action="file/upload" 
								tip=""
								style="width: 100%;text-align: left;"
								:fileUrls="user.zizhirenzheng?user.zizhirenzheng:''" 
								@change="zixunshizizhirenzhengUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="storeupNumber">
							<el-input class="list_inp" v-model="user.storeupNumber"  placeholder="" clearable />
						</el-form-item>
					</el-col>
					<span class="formModel_btn_box">
						<el-button class='confirm_btn' type="primary" @click="onSubmit"></el-button>
					</span>
				</el-row>
			</el-form>
		</div>
	</div>
</template>

<script setup>
	import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/toolUtil";
	import {
		reactive,
		ref,
		getCurrentInstance
	} from 'vue'
	import { useStore } from 'vuex'
	const store = useStore()
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const tableName = ref('zixunshi')
	const user = ref({})
	const zixunshixingbieLists = ref([])
	const init = () => {
		zixunshixingbieLists.value = ",".split(',')
	}
	const zixunshitouxiangUploadSuccess=(fileUrls)=> {
	    user.value.touxiang = fileUrls;
	}
	const zixunshizizhirenzhengUploadSuccess=(fileUrls)=> {
	    user.value.zizhirenzheng = fileUrls;
	}
	const onSubmit = () => {
		if(user.value.touxiang!=null){
			user.value.touxiang = user.value.touxiang.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if((user.value.lianxifangshi)&&(!context?.$toolUtil.isMobile(user.value.lianxifangshi))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(user.value.zizhirenzheng!=null){
			user.value.zizhirenzheng = user.value.zizhirenzheng.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if((user.value.thumbsupNumber)&&(!context?.$toolUtil.isIntNumer(user.value.thumbsupNumber))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if((user.value.crazilyNumber)&&(!context?.$toolUtil.isIntNumer(user.value.crazilyNumber))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if((user.value.storeupNumber)&&(!context?.$toolUtil.isIntNumer(user.value.storeupNumber))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		store.dispatch('user/update',user.value).then(res=>{
			if(res?.data&&res.data.code==0)context?.$toolUtil.message('','success')
		})

	}
	const getInfo = () => {
		context?.$http({
			url: `${tableName.value}/session`,
			method: 'get'
		}).then(res => {
			user.value = res.data.data
			init()
		})
	}
	getInfo()
</script>

