<template>
		<el-dialog v-model="formVisible" :title="''" width="70%" destroy-on-close>
		<div class="center_view">
			<div>
				<div class="list_search_view">
					<el-form :model="searchQuery" class="search_form">
						<div class="search_view">
							<div class="search_label">
								
							</div>
							<div class="search_box">
								<el-input class="search_inp" v-model="searchQuery.questionname" placeholder=""
									clearable>
								</el-input>
							</div>
						</div>
						<div class="search_btn_view">
							<el-button class="search_btn" type="primary" @click="searchClick()"></el-button>
						</div>
					</el-form>
				</div>
			</div>
			<br>
			<el-table v-loading="listLoading" border :stripe='false' ref="table" :data="list">
				<el-table-column label="" width="120" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">{{ scope.$index + 1}}</template>
				</el-table-column>
				<el-table-column label="" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						{{scope.row.username}}
					</template>
				</el-table-column>
				<el-table-column label="" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						{{scope.row.papername}}
					</template>
				</el-table-column>
				<el-table-column label="" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						{{scope.row.questionname}}
					</template>
				</el-table-column>
				<el-table-column label="" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						<el-tag type="success" v-if="scope.row.myscore>0">{{scope.row.myscore}}</el-tag>
						<el-tag type="danger" v-else>{{scope.row.myscore}}</el-tag>
					</template>
				</el-table-column>
				<el-table-column label="" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
                        {{scope.row.type!=2?scope.row.myanswer:{'A':'','B':''}[scope.row.myanswer]}}
					</template>
				</el-table-column>
				<el-table-column label="" :resizable='true' :sortable='true' align="left" header-align="left">
					<template #default="scope">
						{{scope.row.addtime}}
					</template>
				</el-table-column>
			</el-table>
			<el-pagination
				background
				:layout="layouts.join(',')"
				:total="total" 
				:page-size="listQuery.limit"
                v-model:current-page="listQuery.page"
				prev-text="<"
				next-text=">"
				:hide-on-single-page="false"
				:style='{}'
				@size-change="sizeChange"
				@current-change="currentChange"  />
		</div>
		</el-dialog>
</template>

<script setup>
	import {
		ref,
		nextTick,
		getCurrentInstance
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router'
	import {
		ElMessageBox
	} from 'element-plus'
	const formVisible = ref(false)
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	//
	const tableName = 'examrecord'
	const formName = ''
	const route = useRoute()
	//
	const listLoading = ref(false)
	const list = ref([])
	//
	const total = ref(0)
	const layouts = ref(["total","prev","pager","next","sizes","jumper"])
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	//
	//
	const btnAuth = (e, a) => {
		return context?.$toolUtil.isAuth(e, a)
	}
	//
	const searchClick = () => {
		listQuery.value.page = 1
		getList()
	}
	const table = ref(null)
	const listChange = (row) => {
		nextTick(() => {
			table.value.clearSelection()
			table.value.toggleRowSelection(row)
		})
	}
	const listQuery = ref({
		page: 1,
		limit: 20,
	})
	const searchQuery = ref({})
	const selRows = ref([])
	//
	const handleSelectionChange = (e) => {
		selRows.value = e
	}
	//
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		if (searchQuery.value.questionname && searchQuery.value.questionname != '') {
			params['questionname'] = '%' + searchQuery.value.questionname + '%'
		}
		if(paperid.value){
			params['paperid'] = paperid.value
		}
		if(userid.value){
			params['userid'] = userid.value
		}
		context.$http({
			url: `examrecord/page`,
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
		})
	}
	const formRef = ref(null)
	const formModelChange = () => {
		searchClick()
	}
	const infoClick = (row) => {
		nextTick(()=>{
			formRef.value.init(row)
		})
	}
	const paperid = ref(0)
	const userid = ref(0)
	const init = (row) => {
		paperid.value = row.paperid
		userid.value = row.userid
		getList()
		formVisible.value = true
	}
	//
	defineExpose({
		init
	})
	
</script>
<style lang="scss" scoped>
	// 
	.list_search_view {
		// 
		.search_form {
			// 
			.search_view {
				// label
				.search_label {
				}
				// item
				.search_box {
					// 
					:deep(.search_inp) {
					}
				}
			}
			// 
			.search_btn_view {
				width: 20%;
				display: flex;
				padding: 0 20px;
				// 
				.search_btn {
				}
				// -
				.search_btn:hover {
				}
			}
		}
	}
	// 
	.el-table {
		:deep(.el-table__header-wrapper) {
			thead {
				tr {
					th {
						.cell {
						}
					}
				}
			}
		}
		:deep(.el-table__body-wrapper) {
			tbody {
				tr {
					td {
						.cell {
							// 
							.el-button--info {
							}
							// -
							.el-button--info:hover {
							}
						}
					}
				}
				tr:hover {
					td {
					}
				}
			}
		}
	}
</style>